import React, { useState } from 'react'
import './App.css'
import isEqual from 'lodash.isequal'
import { useQuery, useSubscription, useApolloClient, useLazyQuery } from '@apollo/client'
import TopicSelection from './components/TopicSelection'
import Navbar from './components/Navbar'
import OldTopics from './components/OldTopics'
import AddTopic from './components/AddTopic'
import AdminView from './components/AdminView'
import Main from './components/Main'
import { 
  GET_VOTE_STATE, VOTE_ADDED, NEW_ROUND_VOTE_ADDED, GET_SPLITTED_LIST, VOTER_COUNT_CHANGED
} from './queries/queries'
import Cookies from 'js-cookie'
import LoadingScreen from './components/LoadingScreen'

const App = () => {
  const client = useApolloClient()

  const [body, setBody] = useState('main')
  const voteStateQuery = useQuery(GET_VOTE_STATE)
  const [launchGetFreshSplittedList] = useLazyQuery(GET_SPLITTED_LIST, {fetchPolicy: 'network-only'})

  useSubscription(VOTE_ADDED, {
    onSubscriptionData: ({ subscriptionData }) => {
      const newVoteState = subscriptionData.data.voteAdded

      const { getVoteState } = client.readQuery({
        query: GET_VOTE_STATE
      })

      if (!isEqual(newVoteState, getVoteState)) {
        client.writeQuery({
          query: GET_VOTE_STATE,
          data: {
            getVoteState: newVoteState
          }
        })
      }
    }
  })

  useSubscription(VOTER_COUNT_CHANGED, {
    onSubscriptionData: ({ subscriptionData }) => {
      const newVoteState = subscriptionData.data.voterCountChanged

      const { getVoteState } = client.readQuery({
        query: GET_VOTE_STATE
      })

      if (!isEqual(newVoteState, getVoteState)) {
        client.writeQuery({
          query: GET_VOTE_STATE,
          data: {
            getVoteState: newVoteState
          }
        })
      }
    }
  })

  useSubscription(NEW_ROUND_VOTE_ADDED, {
    onSubscriptionData: ({ subscriptionData }) => {
      const newVoteState = subscriptionData.data.newRoundVoteAdded

      const { getVoteState } = client.readQuery({
        query: GET_VOTE_STATE
      })

      if (newVoteState.newRound === true) {
        launchGetFreshSplittedList()
      }

      if (!isEqual(newVoteState, getVoteState)) {
        client.writeQuery({
          query: GET_VOTE_STATE,
          data: {
            getVoteState: newVoteState
          }
        })
      }
    }
  })

  if (voteStateQuery.loading) {
    return (
      <div className='App'>
        <Navbar setBody={setBody} />
        <LoadingScreen />
      </div>
    )
  }

  const voteState = voteStateQuery.data.getVoteState

  if (body === 'main') {
    return (
      <div className="App">
        <Navbar setBody={setBody} />
        <Main setBody={setBody} voteState={voteState} />
      </div>
    )
  }

  if (body === 'topicSelection') {
    return (
      <div className="App">
        <Navbar setBody={setBody} />
        <TopicSelection voteState={voteState} setBody={setBody} Cookies={Cookies} />
      </div>
    )
  }

  //if (body === 'covered') {
  //  return (
  //    <div className='App'>
  //      <Navbar setBody={setBody} />
  //      <OldTopics />
  //    </div>
  //  )
  //}

  if (body === 'addTopic') {
    return (
      <div className='App'>
        <Navbar setBody={setBody} />
        <AddTopic />
      </div>
    )
  }

  if (body === 'adminLogin') {
    return (
      <div className='App'>
        <Navbar setBody={setBody} />
        <AdminView voteState={voteState} />
      </div>
    )
  }

}

export default App
