import React, { useState } from 'react'
import Button from './Button'
import { useMutation } from '@apollo/client'
import { OPEN_VOTE, CLOSE_VOTE, GET_VOTE_STATE, CHANGE_VOTER_COUNT } from '../queries/queries'
import arrowUp from '../resources/arrow-up.png'
import arrowDown from '../resources/arrow-down.png'

const Cookies = require('js-cookie')

const AdminView = ({ voteState }) => {

    const [launchChangeVoterCount] = useMutation(CHANGE_VOTER_COUNT)

    const [password, setPassword] = useState('')
    const [error, setError] = useState('')
    const [success, setSuccess] = useState('')
    const [voteIsOpen, setVoteIsOpen] = useState(voteState.voteOpen)
    const [voterCount, setVoterCount] = useState(voteState.voterCount)

    const [openVote] = useMutation(OPEN_VOTE, {
        refetchQueries: [{ query: GET_VOTE_STATE }]
    })
    const [closeVote] = useMutation(CLOSE_VOTE, {
        refetchQueries: [{ query: GET_VOTE_STATE }]
    })

    setTimeout(() => {
        setError('')
        setSuccess('')
    }, 3000)

    const login = () => {
        if (password === process.env.REACT_APP_ADMIN_PASSWORD) {
            setError('')
            Cookies.set('admin_token', process.env.REACT_APP_ADMIN_TOKEN, { sameSite: 'lax' })
            setPassword('')
            setSuccess('Access granted yo.')
        } else {
            setSuccess('')
            setError('Väärä salasana.')
            setPassword('')
        }
    }

    const openVoting = async () => {
        try {
            await openVote()
            setError('')
            setVoteIsOpen(true)
        } catch (error) {
            setSuccess('')
            setError('Äänestystä ei voitu avata.')
        }
    }

    const closeVoting = async () => {
        try {
            await closeVote()
            setError('')
            setVoteIsOpen(false)
        } catch (error) {
            setSuccess('')
            setError('Äänestystä ei voitu sulkea.')
        }
    }

    const handleVoterIncrease = async () => {
        try {
            await launchChangeVoterCount({
                variables: {
                    count: voterCount + 1
                }
            })
            setVoterCount(voterCount + 1)
        } catch (error) {
            console.log('VoterIncrease failed!', error.message)
        }
    }

    const handleVoterDecrease = async () => {
        try {
            await launchChangeVoterCount({
                variables: {
                    count: voterCount - 1
                }
            })
            setVoterCount(voterCount - 1)
        } catch (error) {
            console.log('VoterDecrease failed!', error.message)
        }
    }

    if (Cookies.get('admin_token') === process.env.REACT_APP_ADMIN_TOKEN) {
        return (
            <div className='Container'>
                <h3 className='Heading'>Admin</h3>
                <div className='Line-separator'></div>
                {error !== '' && <p className='Error'>{error}</p>}
                {voteIsOpen === true && <p>Äänestys on auki.</p>}
                {voteIsOpen === false && <p>Äänestys on suljettu.</p>}
                {voteIsOpen === false && <Button type='Button-aanestys' text='Avaa äänestys' handleClick={() => openVoting()} />}
                {voteIsOpen === true && <Button type='Button-aanestys' text='Sulje äänestys' handleClick={() => closeVoting()} />}
                <div className='Line-separator'></div>
                <h4>Äänestäjiä</h4>
                <div className='AdminView-aanestajia-flex'>
                    <img className='AdminView-arrow-icon' src={arrowUp} alt='Arrow up' onClick={() => handleVoterIncrease()} />
                    <p className='AdminView-voterCount'>{voterCount}</p>
                    <img className='AdminView-arrow-icon' src={arrowDown} alt='Arrow down' onClick={() => handleVoterDecrease()} />
                </div>
            </div>
        )
    }
    return (
        <div className='Container'>
            {error !== '' && <p className='Error'>{error}</p>}
            {success !== '' && <p className='Success'>{success}</p>}
            <input className='Aihe-input' value={password} type='password' onChange={(event) => setPassword(event.target.value)} />
            <Button type='Add-button' text='Login' handleClick={() => login()} />
        </div>
    )
}

export default AdminView
