import React, { useState } from 'react'
import check from '../resources/chosen.png'
import ChoiceButton from './ChoiceButton'
import { useQuery } from '@apollo/client'
import { GET_SPLITTED_LIST } from '../queries/queries'
import LoadingScreen from './LoadingScreen'
import LastVoteLandingPage from '../components/LastVoteLandingPage'

const TopicSelection = ({ voteState, Cookies, setBody }) => {

  const [newRoundAnimation, setNewRoundAnimation] = useState(false)

  const [votedCookie, setVotedCookie] = useState(Cookies.get('voted'))

  const [lever, setLever] = useState(true)

  const pool = useQuery(GET_SPLITTED_LIST)

  if (newRoundAnimation) {
    return (
      <div className='App'>
        <LoadingScreen />
        <h3>Seuraava kierros alkaa...</h3>
      </div>
    )
  }

  if (voteState.round > 1 && voteState.newRound && lever) {
    setLever(false)
    setNewRoundAnimation(true)
    setTimeout(() => {
      setNewRoundAnimation(false)
    }, 3000)
  }

  if (pool.loading) {
    return (
      <div className='App'>
        <div className='Container'>
          <LoadingScreen />
        </div>
      </div>
    )
  }

  if (voteState === null || voteState.voteOpen === false) {
    return (
      <div className='App'>
        <h3 className='Heading'>Äänestys on suljettu.</h3>
      </div>
    )
  }

  const leftList = pool.data.getSplittedList.leftList
  const rightList = pool.data.getSplittedList.rightList

  if (voteState.votes === voteState.voterCount) {
    return (
      <div className='App'>
        <LastVoteLandingPage leftList={leftList} rightList={rightList} voteState={voteState} setBody={setBody} Cookies={Cookies} setVotedCookie={setVotedCookie} />
      </div>
    )
  }

  if (leftList.topics.length === 0) {
    return (
      <div>
        <div className='Container'>
          <h1 className='Heading'>Voittaja-aihe</h1>
          {rightList.topics.map(topic => <p key={topic.id} className='Topic-item'>{topic.name}</p>)}
        </div>
      </div>
    )
  }

  if (rightList.topics.length === 0) {
    return (
      <div>
        <div className='Container'>
          <h1 className='Heading'>Voittaja-aihe</h1>
          {leftList.topics.map(topic => <p key={topic.id} className='Topic-item'>{topic.name}</p>)}
        </div>
      </div>
    )
  }

  if (votedCookie === undefined) {
    return (
      <div>
        <div className='Container'>
          <p>Kierros: {voteState.round}</p>
          <p>Ääniä: {voteState.votes}/{voteState.voterCount}</p>
          <h4 className='Heading'>Aihekategoriat <small>(Valitse jompikumpi)</small></h4>
          <div className='Container-flex'>
            <div className='Container-left'>
              {leftList.topics.map(topic => <p key={topic.id} className='Topic-item'>{topic.name}</p>)}
            </div>
            <div className='Container-right'>
              {rightList.topics.map(topic => <p key={topic.id} className='Topic-item'>{topic.name}</p>)}
            </div>
          </div>
          <div className='TopicSelection-buttons'>
            <ChoiceButton type='TopicSelection-Choice-button' text='Valitse' side='left' Cookies={Cookies} votes={voteState.votes} voterCount={voteState.voterCount} setBody={setBody} setVotedCookie={setVotedCookie} />
            <ChoiceButton type='TopicSelection-Choice-button' text='Valitse' side='right' Cookies={Cookies} votes={voteState.votes} voterCount={voteState.voterCount} setBody={setBody} setVotedCookie={setVotedCookie} />
          </div>
        </div>
      </div>
    )
  } else {
    return (
      <div>
        <div className='Container'>
          <p>Kierros: {voteState.round}</p>
          <p>Ääniä: {voteState.votes}/{voteState.voterCount}</p>
          <h4 className='Heading'>Aihekategoriat</h4>
          <div className='Container-flex'>
            <div className='Container-left'>
              {leftList.topics.map(topic => <p key={topic.id} className='Topic-item'>{topic.name}</p>)}
            </div>
            <div className='Container-right'>
              {rightList.topics.map(topic => <p key={topic.id} className='Topic-item'>{topic.name}</p>)}
            </div>
          </div>
          <img className='Chosen-img' src={check} alt='Checked icon' />
          <h3>Valittu!</h3>
          <p></p>
          <p className='TopicSelection-footer-text'>Seuraava kierros alkaa kun kaikki ovat äänestäneet.</p>
        </div>
      </div>
    )
  }
}

export default TopicSelection
