import React from 'react'
import Button from './Button'

const Main = ({ setBody, voteState }) => {

  return (
    <div className='Container'>
      <h3 className='Heading'>Aihepoolivalintasovellus</h3>
      {voteState.voteOpen === false && <p>Äänestys on suljettu.</p>}
      {voteState.voteOpen === true && <Button type='Button-aanestys' text='Äänestä' handleClick={() => setBody('topicSelection')} />}
    </div>
  )
}

export default Main
