import { gql } from '@apollo/client'

const VOTESTATE_DETAILS = gql`
  fragment voteStateDetails on VoteState {
      round
      votes
      voteOpen
      newRoundVotes
      newRound
      voterCount
  }
`

export const GET_SPLITTED_LIST = gql`
query {
  getSplittedList  {
    leftList {
      topics {
        id
        name
      }
      votes
    }
    rightList {
      topics {
        id
        name
      }
      votes
    }
  }
}
`

export const ADD_VOTE = gql`
mutation addVote($vote: Int!) {
  addVote(vote: $vote) {
      ...voteStateDetails
  }
}
${VOTESTATE_DETAILS}`

export const GET_COVERED = gql`
query {
  getCovered  {
    id
    name
  }
}
`

export const CREATE_TOPIC = gql`
mutation createTopic($name: String!) {
  addTopic(
    name: $name,
  ) {
    id
    name
  }
}
`

export const OPEN_VOTE = gql`
mutation {
  openVote {
    ...voteStateDetails
  }
}
${VOTESTATE_DETAILS}`

export const CLOSE_VOTE = gql`
mutation {
  closeVote {
    ...voteStateDetails
  }
}
${VOTESTATE_DETAILS}`

export const GET_VOTE_STATE = gql`
query {
  getVoteState {
    ...voteStateDetails
  }
}
${VOTESTATE_DETAILS}`

export const VOTE_ADDED = gql`
  subscription {
    voteAdded {
      ...voteStateDetails
    }
  }
  ${VOTESTATE_DETAILS}`

  export const CHANGE_VOTER_COUNT = gql`
  mutation changeVoterCount($count: Int!) {
    changeVoterCount(
      count: $count,
    ) {
      ...voteStateDetails
    }
  }
  ${VOTESTATE_DETAILS}`

  export const NEW_ROUND_VOTE_ADDED = gql`
    subscription {
      newRoundVoteAdded {
        ...voteStateDetails
      }
    }
    ${VOTESTATE_DETAILS}
  `
  export const VOTE_NEW_ROUND = gql`
    mutation {
      voteNewRound {
        ...voteStateDetails
      }
    }
    ${VOTESTATE_DETAILS}
  `

  export const VOTER_COUNT_CHANGED = gql`
  subscription {
    voterCountChanged {
      ...voteStateDetails
    }
  }
  ${VOTESTATE_DETAILS}
`
